import { ICellRendererComp, ICellRendererParams } from 'ag-grid-community';
import { Component, h, Element, State, Host } from '@stencil/core';
import { IClickCell } from '../../../../types/table-cells';
import { CSSProperties } from '../../../../types/styles';

export interface IMultiBtnCellParams extends IClickCell {
    containerStyles?: CSSProperties;
    buttons?: IBtnParams[] | ((params: ICellRendererParams) => IBtnParams[]);
}

export interface IBtnParams {
    value: string;
    containerStyles?: CSSProperties;
    click?: (params: ICellRendererParams) => any;
    disabled: boolean;
    tooltip?: string;
    hidden?: boolean;
    dataNameValue?: string;
}

interface InitParams extends IMultiBtnCellParams, ICellRendererParams {}

/**
 * @category Table Cell
 * @description A cell component for displaying multiple buttons.
 */

@Component({
    tag: 'atui-multi-btn-cell',
    shadow: false,
})
export class AtuiMultiBtnCell implements ICellRendererComp {
    @Element() el: HTMLElement;
    @State() params: InitParams;
    @State() buttons: IBtnParams[];
    @State() containerStyles: CSSProperties;

    init(params: InitParams): void {
        this.params = params;

        const { containerStyles, buttons } = params;

        this.containerStyles = containerStyles;
        this.buttons =
            typeof buttons === 'function' ? buttons(params) : buttons;
    }

    getGui(): HTMLElement {
        return this.el;
    }

    refresh(params: InitParams): boolean {
        this.updateCell(params.value);
        return true;
    }

    private updateCell(newValue: any) {
        this.params.value = newValue;
    }

    handleClick(btn: IBtnParams): any {
        return btn.click && btn.click(this.params);
    }

    get buttonsToRender(): any {
        return this.buttons.map((button) => {
            return (
                <atui-tooltip
                    position="right"
                    is_visible={!!button.tooltip}
                >
                    {button && (
                        <atui-button
                            type="secondaryText"
                            label={button.value}
                            disabled={button.disabled}
                            data-name={button.dataNameValue}
                            onAtuiClick={() => this.handleClick(button)}
                        ></atui-button>
                    )}

                    {button && button.tooltip && (
                        <span
                            slot="tooltip-content"
                            class={`leading-normal`}
                        >
                            {button.tooltip}
                        </span>
                    )}
                </atui-tooltip>
            );
        });
    }

    render() {
        return (
            <Host class="flex h-full items-center leading-[100%]">
                {this.buttonsToRender}
            </Host>
        );
    }
}
